<template>
  <el-drawer
    title="Novo Campo"
    :visible.sync="visible"
    direction="rtl"
    size="50%"
    :before-close="confirmToClose"
  >
    <div id="NewField">
      <div v-loading="loading">
        <p>
          <span>Objeto Selecionado:</span>
          {{ object }}
        </p>

        <p>
          <span>Nome do Campo</span>
          <el-input placeholder="Nome do Campo" v-model="form.label"></el-input>
        </p>

        <p>
          <span>Nome da API</span>
          <el-input placeholder="Ex: first_name" v-model="form.label_api">
            <template slot="prepend">cf_</template>
          </el-input>
        </p>

        <p>
          <span>Obrigatório</span>
          <br />
          <el-switch v-model="form.is_required"></el-switch>
        </p>

        <p>
          <span>Oculto</span>
          <br />
          <el-switch v-model="form.is_hidden"></el-switch>
        </p>

        <p>
          <span>Tipo do Campo</span>
          <br />
          <el-select v-model="form.type_value" placeholder="Tipo do Campo">
            <el-option
              v-for="item in form.type_options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </p>

        <p v-if="optionsDropdownField()">
          <el-tag
            :key="tag"
            v-for="tag in dropdownValues"
            closable
            :disable-transitions="false"
            @close="handleClose(tag)"
          >{{tag}}</el-tag>
          <el-input
            class="input-new-tag"
            v-if="inputVisible"
            v-model="inputValue"
            ref="saveTagInput"
            size="mini"
            @keyup.enter.native="handleInputConfirm"
            @blur="handleInputConfirm"
          ></el-input>
          <el-button v-else class="button-new-tag" size="small" @click="showInput">+ Nova Opção</el-button>
        </p>

        <p>
          <small
            v-if="optionsDropdownField()"
            @click="toggleBulkImportOptions()"
            style="cursor: pointer"
          >*Se quiser importar as opções em massa, basta clicar aqui</small>
        </p>

        <div v-if="this.form.bulkOptionsValuesImportShow == true" style="padding-bottom:20px;">
          <el-input
            type="textarea"
            :rows="5"
            placeholder="Opção 1,Opção 2"
            v-model="form.bulkImportOptions"
          ></el-input>
          <p>
            <small>Digite as opções separando-as por vírgula.</small>
          </p>
          <p>
            <el-button plain size="small" @click="bulkImportOptionsAction()">Importar Valores</el-button>
          </p>
        </div>

        <el-button type="primary" @click="requestNewField()">Criar Campo</el-button>
      </div>
    </div>
  </el-drawer>
</template>

<script>
export default {
  name: "SettingsCustomFieldsNewField",
  props: ["object", "visible"],
  data() {
    return {
      title: "Novo Campo",
      loading: false,
      form: {
        bulkImportOptions: "",
        bulkOptionsValuesImportShow: false,
        label_api: "",
        label: "",
        is_required: false,
        is_hidden: false,
        type_options: [
          {
            value: "text",
            label: "Texto",
          },
          {
            value: "switch",
            label: "Booleano",
          },
          {
            value: "date",
            label: "Data",
          },
          {
            value: "dropdown",
            label: "Dropdown",
          },
        ],
        type_value: "",
      },
      dropdownValues: [],
      inputVisible: false,
      inputValue: "",
    };
  },
  methods: {
    bulkImportOptionsAction() {
      if (this.form.bulkImportOptions == "") {
        this.$notify.error("Você precisa ao menos adicionar 1 opção");
        return false;
      }

      let explode = this.form.bulkImportOptions.split(',')

      explode.forEach(element => {
        this.dropdownValues.push(element)
      });

      this.form.bulkImportOptions = '';

      // ;
    },
    confirmToClose(done) {
      this.$confirm("Você tem certeza que deseja sair?")
        .then((_) => {
          _;
          this.clearFields();
          this.$emit("done");
          done();
        })
        .catch((_) => {
          _;
        });
    },
    toggleBulkImportOptions() {
      this.form.bulkOptionsValuesImportShow = !this.form
        .bulkOptionsValuesImportShow;
    },
    handleClose(tag) {
      this.dropdownValues.splice(this.dropdownValues.indexOf(tag), 1);
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick((e) => {
        e;
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.dropdownValues.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = "";
    },
    optionsDropdownField() {
      if (this.form.type_value === "dropdown") {
        return true;
      } else {
        return false;
      }
    },
    clearFields(clear = true) {
      if (clear === true) {
        this.form.label = "";
        this.form.label_api = "";
        this.form.is_required = false;
        this.form.is_hidden = false;
        this.form.type_value = "";
        this.form.dropdown_values = [];
      }

      this.loading = false;
    },
    requestNewField() {
      this.loading = true;
      var data = {
        label: this.form.label,
        label_api: "cf_" + this.form.label_api,
        is_required: this.form.is_required,
        is_hidden: this.form.is_hidden,
        type: this.form.type_value,
        dropdown_values: this.dropdownValues,
        object: this.object,
      };

      if (
        this.form.label == "" ||
        this.form.label_api == "" ||
        this.form.type_value == ""
      ) {
        this.loading = false;
        this.$notify.error({
          title: "Erro",
          message: "Todos os campos devem ser preenchidos",
        });

        return false;
      }

      this.$http
        .post("/fields", data)
        .then((res) => {
          if (res.status == 201) {
            this.clearFields();
            this.$emit("done");

            this.$notify({
              title: "Sucesso",
              message: res.data.msg,
              type: "success",
            });
          } else {
            this.$notify.error({
              title: "Erro",
              message: "Houve um erro",
            });
          }
        })
        .catch((err) => {
          this.clearFields(false);
          this.$notify.error({
            title: "Erro",
            message: err.response.data,
          });
        });
    },
  },
};
</script>

<style lang="scss">
#NewField {
  .el-tag + .el-tag {
    margin-left: 10px;
  }
  .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
}
</style>