<template>
  <div>
    <el-row>
      <el-col :xs="24" :sm="24" :md="16" :lg="16" :xl="16">
        <h1 class="pageTitle">{{ this.pageTitle }}</h1>
      </el-col>
      <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8" class="text-right">
        <el-select
          v-model="objectValue"
          clearable
          placeholder="Selecione o Objeto"
          @change="changeObject()"
        >
          <el-option
            v-for="item in objects"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-button plain @click="newField()">Criar Campo</el-button>
      </el-col>

      <el-col id="listData" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
        <el-row>
          <el-col :span="24">
            <div v-if="noData()" class="text-center" id="noContent">
              <i class="fas fa-ghost"></i>
              <h2 class="title">Selecione um objeto para carregar os campos</h2>
            </div>
            <div v-if="!noData()">
              <div v-loading="loading" class="content">
                <el-card
                  class="box-card field-card"
                  v-for="(field) in fields"
                  :key="field.field_id"
                >
                  <span>{{ field.field_label }}</span>
                  <el-button-group style="float: right">
                    <el-button
                      size="mini"
                      plain
                      icon="el-icon-edit"
                      @click="editField(field.field_id)"
                    ></el-button>
                    <el-button
                      size="mini"
                      @click="deleteField(field.field_id)"
                      plain
                      icon="el-icon-delete"
                    ></el-button>
                  </el-button-group>
                </el-card>
              </div>
            </div>
          </el-col>
        </el-row>

        <NewField :object="this.objectValue" :visible="drawer.newVisible" @done="changeObject()" />
        <EditField :fieldID="this.updateFieldID" :visible="drawer.editVisible" />
      </el-col>
    </el-row>
  </div>
</template>

<script>
import NewField from "@/views/restricted/Settings/Fields/CustomFieldsNew";
import EditField from "@/views/restricted/Settings/Fields/UpdateField";
export default {
  name: "SettingsCustomFields",
  components: { NewField, EditField },
  data() {
    return {
      drawer: {
        newVisible: false,
        editVisible: false,
      },
      updateFieldID: '',
      newFieldVisible: false,
      fields: [],
      loading: false,
      pageTitle: "Campos Personalizados",
      objects: [
        {
          value: "leads",
          label: "Leads",
        },
        {
          value: "accounts",
          label: "Contas",
        },
        {
          value: "contacts",
          label: "Contatos",
        },
        {
          value: "deals",
          label: "Oportunidades",
        },
      ],
      objectValue: "",
    };
  },
  head: {
    title: {
      inner: "Configurações | Campos Personalizados",
      complement: "GuiaSales",
    },
  },
  methods: {
    getModulesForSelectObject() {
      this.$http.get("/modules").then((res) => {
        if (res.status == 200) {
          var arr = [];
          var d = res.data;
          d.forEach((e) => {
            var element = {
              label: e.module_label,
              value: e.module_table_name,
            };
            arr.push(element);
          });
          this.objects = arr;
        }
      });
    },
    newField() {
      if (this.objectValue == "") {
        this.$notify.error({
          title: "Erro",
          message: "Escolha um objeto para criar o campo",
        });
        return false;
      }
      this.drawer.newVisible = true;
    },
    editField(id) {
      this.updateFieldID = id;
      this.drawer.editVisible = true;

      
      // this.$http.get("/field/" + id).then((res) => {
      //   if (res.status == "200") {
      //     this.drawer.update.title = "Campo - " + res.data.field_label;
      //     this.drawer.update.visible = true;
      //   } else {
      //     this.$notify.error({
      //       title: "Erro",
      //       message: "Houve um erro na solicitação do campo",
      //     });
      //   }
      // });
    },
    deleteField(id) {
      this.$confirm(
        "Você apagará o campo e não poderá voltar. Deseja continuar?",
        "Atenção",
        {
          confirmButtonText: "Deletar",
          cancelButtonText: "Cancelar",
          type: "warning",
        }
      ).then(() => {
        this.$http
          .delete("/field/" + id)
          .then((res) => {
            if (res.status == "200") {
              this.$message({
                type: "success",
                message: "Campo deletado!",
              });
              this.changeObject();
            } else {
              this.$notify.error({
                title: "Erro",
                message: "Mensagem:" + res.data,
              });
            }
          })
          .catch((err) => {
            this.$notify.error({
              title: "Erro",
              message: err.response.data.msg,
            });
          });
      });
    },
    changeObject() {
      this.drawer.newVisible = false;
      if (this.objectValue != "") {
        this.$http.get("/fields/" + this.objectValue).then((res) => {
          this.loading = false;
          if (res.status == "200") {
            this.fields = res.data;
          } else {
            this.$notify.error({
              title: "Erro",
              message: "Houve um erro na solicitação",
            });
          }
        });
      }
    },
    noData() {
      if (this.objectValue == "") {
        return true;
      } else {
        return false;
      }
    },
  },
  mounted() {
    this.getModulesForSelectObject();
  },
};
</script>

